/* basic style */

.cp-preloader {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: 999;
	transform: translate3d(-50%, -50%, 0);
  }
  
  .cp-preloader__letter {
	display: inline-block;
	position: relative;
	overflow: hidden;
	color: rgba(0, 0, 0, 0.3);
  
	&:before {
	  content: attr(data-preloader);
	  position: absolute;
	  top: 0;
	  left: 0;
	  animation-duration: 3s;
	  animation-iteration-count: infinite;
	  animation-fill-mode: backwards;
	}
  
	&:nth-child(2):before {
	  animation-delay: 0.3s;
	}
  
	&:nth-child(3):before {
	  animation-delay: 0.45s;
	}
  
	&:nth-child(4):before {
	  animation-delay: 0.6s;
	}
  
	&:nth-child(5):before {
	  animation-delay: 0.75s;
	}
  
	&:nth-child(6):before {
	  animation-delay: 0.9s;
	}
  
	&:nth-child(7):before {
	  animation-delay: 1.05s;
	}
  }
  
  /* type 1 */
  
  .cp-preloader_type1 .cp-preloader__letter:before {
	animation-name: cp-preloader1;
  }
  
  @keyframes cp-preloader1 {
	0%, 100% {
	  transform: rotateY(-90deg);
	  opacity: 0;
	}
  
	20%, 50% {
	  transform: rotateY(0);
	  opacity: 1;
	}
  
	65% {
	  transform: rotateY(0);
	  opacity: .3;
	}
  
	80% {
	  opacity: 0;
	}
  }
  
  /* type 2 */
  
  .cp-preloader_type2 .cp-preloader__letter:before {
	animation-name: cp-preloader2;
  }
  
  @keyframes cp-preloader2 {
	0%, 75%, 100% {
	  transform: rotateY(-90deg);
	  opacity: 0;
	}
  
	20%, 58% {
	  transform: rotateY(0);
	  opacity: 1;
	}
  }
  
  /* type 3 */
  
  .cp-preloader_type3 .cp-preloader__letter:before {
	transform-origin: 0 50%;
	animation-name: cp-preloader3;
  }
  
  @keyframes cp-preloader3 {
	0%, 100% {
	  transform: rotateY(-90deg);
	  opacity: 0;
	}
  
	20%, 50% {
	  transform: rotateY(0);
	  opacity: 1;
	}
  
	65% {
	  transform: rotateY(0);
	  opacity: .3;
	}
  
	80% {
	  opacity: 0;
	}
  }
  
  /* type 4 */
  
  .cp-preloader_type4 .cp-preloader__letter:before {
	animation-name: cp-preloader4;
  }
  
  @keyframes cp-preloader4 {
	0%, 100% {
	  transform: rotateY(-90deg) scale(1);
	  opacity: 0;
	}
  
	20%, 50% {
	  transform: rotateY(0) scale(1);
	  opacity: 1;
	}
  
	75% {
	  transform: rotateY(0) scale(1.2);
	  opacity: 0;
	}
  }
  
  /* type 5 */
  
  .cp-preloader_type5 .cp-preloader__letter:before {
	animation-name: cp-preloader5;
  }
  
  @keyframes cp-preloader5 {
	0%, 100% {
	  transform: rotateY(-90deg) scale(1);
	  opacity: 0;
	}
  
	20%, 50% {
	  transform: rotateY(0) scale(1);
	  opacity: 1;
	}
  
	65% {
	  transform: rotateY(0) scale(0);
	  opacity: 0;
	}
  }
  
  /*
  =====
  DEMO  PRELOADERS
  =====
  */
  
  .cp-preloader__letter {
	font-size: 6rem;
	letter-spacing: 1rem;
  
	&:before {
	  color: #fff;
	}
  }
  
  /*
  =====
  DEMO
  =====
  */
  
  @media (max-width: 767px) {
	html {
	  font-size: 20%;
	}
  }
  
  @media (min-width: 768px) {
	html {
	  font-size: 62.5%;
	}
  }
  
  
